import React from 'react'
import ReactDOM from 'react-dom'
import { Card, Grid, Divider, FormControl, MenuItem, Select, TextField, Button, Icon, Typography } from '@material-ui/core'
import {Add, Money, TrendingUp} from '@material-ui/icons'


import basket from './assets/basket.jpg'

class AddSales extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            itemsSample : [{
                name : "Tomato",
                id : "Tom331",
                price : 20.0
            }, {name : "Onion", id : "Oni312", price : 30.0}],
            quantity : 0.0,
            selected : "Tom331"
        }
    }

    render() {
        return (
            <div>
                <Card style = {{width : "100%"}}>
                    <Grid container spacing = {3}>
                        <Grid item xs = {8}>
                           <div style = {{margin : "20px auto", textAlign : 'left', padding : '10px'}}>
                               <Typography variant = "primary">Update Sales information : </Typography>
                               <Divider />
                               <div>
                                   <p>Product</p>
                                   <FormControl>
                                       <Select value = {this.state.selected} onChange = {(e) => {
                                           this.setState({selected : e.target.value})
                                       }}>
                                           {
                                               this.state.itemsSample.map((k, v) => {
                                                   return (
                                                       <MenuItem value = {k.id}>{k.name + " Price : "+ k.price}</MenuItem>
                                                   )
                                               })
                                           }
                                       </Select>
                                   </FormControl>
                                   <div style = {{marginTop : '20px'}}>
                                       <FormControl>
                                       <TextField
                                            id="outlined-name"
                                            label="Quantity"
                                            value={this.state.quantity}
                                            onChange = {(e) => {this.setState({quantity : e.target.value})}}
                                            margin="normal"
                                            variant="outlined"
                                        />
                                       </FormControl>
                                   </div>
                                   <div style = {{marginTop : '20px'}}>
                                       <Button variant = "contained" color = "primary"><Icon><Add></Add></Icon>  Add</Button>,
                                   </div>
                               </div>
                           </div>
                        </Grid>
                        <Grid item xs = {4}>
                            <div style = {{height : '400px',width : "100%"}}>
                                <img src = {basket} />
                            </div>
                        </Grid>
                    </Grid>
                </Card>
            </div>
        )
    }
}

class ShowTotalSales extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            totalAmount : 9541.5,
            recentTransaction : {
                product : "Tomato",
                quantity : 32.0,
                price : 20.0 * 32.0
            }
        }
    }

    render() {
        return (
            <div>
                <Card style = {{margin : '20px auto'}}>
                    <div style = {{padding : '20px', textAlign : 'left'}}>
                        <Typography>Amount earned today : </Typography>
                        <Typography><Money /> {this.state.totalAmount} Rs</Typography>
                        <Divider />
                        <Typography>Recent Transaction Detail : </Typography>
                        <p>{this.state.recentTransaction.product} , Quantity : {this.state.recentTransaction.quantity}, Amount : {this.state.recentTransaction.price}</p>
                    </div>
                </Card>
            </div>
        )
    }
}

class ShowTotalQuantity extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            totalQuantity : 500.0,
            maxQuantity : {
                product : "Tomato",
                quantity : 32.0,
                price : 20.0 * 32.0
            }
        }
    }

    render() {
        return (
            <div>
                <Card style = {{margin : '20px auto'}}>
                    <div style = {{padding : '20px', textAlign : 'left'}}>
                        <Typography>Quantity sold : </Typography>
                        <Typography><Money /> {this.state.totalQuantity} Rs</Typography>
                        <Divider />
                        <Typography><TrendingUp /> Maximum Sold Quantity of the day : </Typography>
                        <p>{this.state.maxQuantity.product} , Quantity : {this.state.maxQuantity.quantity}, Amount : {this.state.maxQuantity.price}</p>
                    </div>
                </Card>
            </div>
        )
    }
}

class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
    }

    render() {
        return (
            <div style = {{paddingLeft : '250px', width : '80%', paddingTop : "70px"}}>
                <div style = {{marginLeft : ''}}>
                <Grid container spacing = {3}>
                    <Grid item xs = {7}>
                        <AddSales userData = {this.props.userData}/>
                    </Grid>
                    <Grid item xs = {5}>
                        <ShowTotalSales userData = {this.props.userData}/>
                        <ShowTotalQuantity userData = {this.props.userData}/>
                    </Grid>
                </Grid>
                </div>
            </div>
        )
    }
}

export default Home